body {
	margin: 0;
	padding: 0;

	display: flex;

	width: 100vw;
	height: 100vh;

	flex-direction: column;
}

footer {
	position: fixed;
	bottom: 0;
	left: 0;
	background-color: whitesmoke;
	width: 100vw;
}

.react-gauge {
	margin: 0 auto;
	width: 100%;
	height: 100%;
}

.range {
	width: 200px;
	height: 50px;
}

img {
	width: 80%;
	padding: 20px;
}

main {
	flex: 1 0 auto;
}
